}

*
{
    margin: 0;
    padding: 0;
}
.sign-in-container 
{
    width:500px;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.87);
box-shadow: 0px 0px 38px -6px rgba(0,0,0,0.61);
text-align: center;
border-radius: 10px;
display: flex;
justify-content:center;
align-items:center;

}
.submit-button
{
    width:300px;
    height:40px;
}
.land-container
{
    width:100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items: center;
}

#outlined-basic {
    width:300px;
    height: 20px;
    
}

@media only screen and (max-width: 600px) {
    .sign-in-container {
        width: 350px;
        height: 500px;
        
        
    }
  }
  @media only screen and (max-width: 400px) {
    .sign-in-container {
        width: 80%;
        height: 700px;
        
        
    }
    #outlined-basic {
        width:200px;
        height: 20px;
        
    }
    .submit-button
    {
        width:200px;
        height:40px;
    }
  }
