}
.button-link {
    text-decoration: none;
}
.main-info {
    width: 500px;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.809);
    margin-left: auto;
    margin-right: auto;
    color: rgb(169, 168, 168);
box-shadow: -1px 6px 36px -1px rgba(0,0,0,0.75);
display: flex;
justify-content:center;
border-radius: 10px;
align-items:center;
    
}
.info-content {
    width: 70%;
    margin-right: auto;
    margin-left: auto;
    text-align: justify;
    
}
.info-content p
{
    font-size: 18px;
    
}
.button
{
    width:300px;
    height: 50px;
    
}
.info-title {
    font-family: 'Rajdhani', sans-serif;
    color: #9d9d9d;
    text-shadow: 0px 5px 3px rgba(150, 150, 150, 1);
    font-weight: 500;
    
}

@media only screen and (max-width: 600px) {
    .main-info {
