@import url('https://fonts.googleapis.com/css2?family=Rajdhani&display=swap');
*
{
    margin: 0;
    padding: 0;
    font-family: 'Rajdhani', sans-serif;
}
.errorMsg {
    color: red;
    font-family: 'Rajdhani', sans-serif;
}

.RightMsg
{
    color: green;
    font-family: 'Rajdhani', sans-serif;
}

.register 
{
    width:500px;
    height: auto;
    min-height: 700px;
    background-color: rgba(255, 255, 255, 0.87);
    -webkit-box-shadow: 0px 0px 38px -6px rgba(0,0,0,0.61);
-moz-box-shadow: 0px 0px 38px -6px rgba(0,0,0,0.61);
box-shadow: 0px 0px 38px -6px rgba(0,0,0,0.61);
text-align: center;
border-radius: 10px;
display: flex;
justify-content:center;
align-items:center;

}
.submit-button
{
    width:300px;
    height:40px;
}
.register-container
{
    width:100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items: center;
}

#outlined-basic {
    width:300px;
    height: 10px;
    
}
@media only screen and (max-width:1600px) {
    
    .register 
{
    width:400px;
    min-height: 600px;
   

}
#outlined-basic {
    width:300px;
    height: 1px;
    
}
  }
@media only screen and (max-width:1300px) {
    
    .register 
{
    width:400px;
    min-height: 600px;
   

}
#outlined-basic {
    width:300px;
    height: 1px;
    
}
  }
@media only screen and (max-width: 600px) {
    .sign-in-container {
        width: 350px;
        height: 500px;
        
        
    }
  }

  @media only screen and (max-width: 400px) {
    .sign-in-container {
        width: 80%;
        height: 700px;
        
        
    }
    .register 
    {
        width:300px;
        min-height: 600px;
       
    
    }
    #outlined-basic {
        width:200px;
        height: 10px;
        
    }
    .submit-button
    {
        width:200px;
        height:40px;
    }
  }